import React, { useEffect, useState } from 'react'
import { StaticImage } from 'gatsby-plugin-image';
import loadable from '@loadable/component'
import { Link } from 'react-scroll'
import styled from '@emotion/styled';
import { useWindowScroll, useWindowSize } from 'react-use';

import Box from 'components/Box';
import Flex from 'components/Flex';
import { responsive } from '../components/ThemeProvider/theme';
const Menu = loadable(() => import('./Menu'))

const StyledHeader = styled(Flex)`
&::before {
  transition: 500ms opacity;
}
`

const Header = ({ onOpen, ...props }) => {
  const [dark, setDark] = useState()
  const { y } = useWindowScroll();
  const windowSize = useWindowSize()
  useEffect(() => {
    setDark(y > windowSize.height)
  }, [y, windowSize.height])
  return (
    <StyledHeader
      as="header"
      position="fixed"
      _before={{
        content: '""',
        display: 'block',
        position: 'absolute',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        bgImage: 'linear-gradient(90deg, #3AAE36 3%, #008667 35.96%, #006464 97.46%)',
        opacity: 1 - dark,
      }}
      bg="white"
      top={0}
      left={0}
      right={0}
      alignItems="center"
      justifyContent="space-between"
      zIndex="docked"
      overflow="hidden"
      {...props}
    >
      <Box.Relative width={responsive('11.25em', '14.625em')} mx={responsive('1em', '2em')}>
        <Link to="intro" smooth offset={-78} style={{ cursor: 'pointer' }}>
          <StaticImage src="logo_white.png" alt="logo white" />
          <Box.FullAbs opacity={+dark}>
            <StaticImage placeholder="none" src="logo.png" alt="logo" />
          </Box.FullAbs>
        </Link>
      </Box.Relative>
      <Menu modalonOpen={onOpen} dark={dark} />
    </StyledHeader>
  )
}

export default Header
