import React from 'react'
import Box from './Box'
import { responsive } from './ThemeProvider/theme'

const Board = ({ children, ...props }) => {
  return (
    <Box
      width="100%"
      px={responsive('0.5em', 0)}
      borderRadius="1em"
      background="linear-gradient(rgba(255, 255, 255) 0%, rgba(255, 255, 255, 0))"
      {...props}
    >
      {children}
    </Box>
  )
}

export default Board
