import React, { forwardRef, useMemo } from 'react'
import styled from '@emotion/styled'
// import Headroom from 'react-headroom'
import loadable from '@loadable/component'

import FloatButton from '../FloatButton'
import { Media } from '../../components/ThemeProvider/theme';

const BootomNav = forwardRef(({ onOpen, setPin }, ref) => {
  const fallback = <FloatButton onOpen={onOpen} ref={ref} />
  const Headroom = useMemo(() => loadable(() => import('react-headroom'), { fallback }), [])

  const StyledHeadroom = useMemo(() => styled(Headroom)`
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-Index: 1;
    .headroom {
      transition: transform 200ms ease-in-out;
    }
    .headroom--unpinned {
      transform: translateY(100%);
    }
    .headroom--pinned {
      transform: translateY(0%);
    }
  `, [Headroom])
  return (
    <>
      <Media lessThan="tablet">
        <StyledHeadroom onPin={() => setPin(true)} onUnpin={() => setPin(false)} disableInlineStyles>
          {fallback}
        </StyledHeadroom>
      </Media>
      <Media greaterThanOrEqual="tablet">
        {fallback}
      </Media>
    </>
  )
})

export default BootomNav
