import React from 'react'
import Box from './Box'
import Text from './Text'
import { Media, responsive } from './ThemeProvider/theme'

const Source = ({ borderColor, children, note, chart, ...props }) => {
  return (
    <Box
      mt="1.875rem"
      px={responsive('5%', '10%')}
      pt="1rem"
      pb="5em"
      color="custom.textSource"
      fontSize={responsive('0.5em', '0.75em')}
      borderTop="1px solid"
      borderColor={borderColor}
      textAlign="justify"
      whiteSpace="pre-wrap"
      {...props}
    >
      {children}
    </Box>
  )
}

export default Source
