import React from "react";
import { ImYoutube, ImFacebook } from "react-icons/im";
import { SiLine } from "react-icons/si";
import { RiMoneyDollarCircleFill } from "react-icons/ri";
import { StaticImage } from "gatsby-plugin-image";
import { Icon } from "@chakra-ui/react";

import Box from "../components/Box";
import { responsive } from "../components/ThemeProvider/theme";
import LazyLoad from "../components/LazyLoad";

export const size = responsive('1.5rem', '2rem')

const icons =  {
  youtube: <Icon as={ImYoutube} w={size} h={size} />,
  line: <Icon as={SiLine} w={size} h={size} />,
  facebook: <Icon as={ImFacebook} w={size} h={size} />,
  coin: <Icon as={RiMoneyDollarCircleFill} w={size} h={size} />,
  bank: <Box w={size}><LazyLoad ratio={1}><StaticImage src="bank.png" alt="小豬撲滿" /></LazyLoad></Box>,
}

export default icons
