const trackEvent = (action, category, label, gaOnly) => {
  if (process.env.NODE_ENV === 'development') {
    console.log(action, category, label)
  }
  if (window.ga) {
    window.ga('send', 'event', category, action, label);
  }
  if (window.gtag && !gaOnly) {
    window.gtag('event', action, {
      'event_category' : category,
      'event_label' : label,
    });
  }
}

export const trackClick = (buttonName) => trackEvent('Click', 'CampaignSite', `ESGEMD-${buttonName}`)
export const trackVideo = (percent) => trackEvent('Play', 'Video', `ESGEMD-Video-${percent}`, true)
export const trackView = (name, title) => {
  if (process.env.NODE_ENV === 'development') {
    console.log('pageview', name, title)
  }

  const pathname = `${window.location.pathname}/#${name}`
  if (window.ga) {
    window.ga('send', 'pageview', pathname);
  }
  if (window.gtag) {
    window.gtag('event', 'page_view', {
      'page_title' : title,
      'page_path' : pathname,
    });
  }
}

export default trackEvent
