import React, { useEffect, useRef, useState, forwardRef } from 'react';
import styled from '@emotion/styled';
import loadable from '@loadable/component'

// import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

import Box from './Box'
import Chevron from './Chevron'
import { Media } from './ThemeProvider/theme'
import LazyLoad from './LazyLoad';
const Slider = loadable(() => import('react-slick'))

const StyleBox = styled(Box)`
  .slick-slider,
  .slick-list,
  .slick-slide,
  .slick-slide > div {
    height: 100%;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
    height: 100%;
  }
  .slick-arrow {
    height: auto;
    font-size: 0.75em;
    z-index: 1;
    color: black;
    position: absolute;
    &::before {
      content: none;
    }
  }
  .slick-prev {
    left: ${props => props.isMobile ? '-3em' : '0'};
    cursor: ${props => !props.activeItemIndex && 'not-allowed'};
    opacity: ${props => !props.activeItemIndex && 0.5};
    width: ${props => props.arrowWidth || '2.5em'};
  }
  .slick-next {
    right: ${props => props.isMobile ? '-3em' : '0'};
    opacity: ${props => (props.activeItemIndex == props.length - 1) && 0.5};
    cursor: ${props => (props.activeItemIndex == props.length - 1) && 'not-allowed'};
    width: ${props => props.arrowWidth || '2.5em'};
  }
`

const MySlider = forwardRef(({
  isMobile,
  children,
  width,
  autoplay,
  arrows,
  slidesToShow,
  silderProps,
  typeIndex,
  setType,
  handleCange,
  prevArrow,
  nextArrow,
  ...props
}, ref) => {
  const [activeItemIndex, setActiveIndex] = useState(0)
  const slickRef = useRef()
  const theRef = ref || slickRef
  useEffect(() => {
    if (theRef.current) {
      theRef.current.slickGoTo(typeIndex)
    }
  }, [theRef, typeIndex])
  const arrowWidth = isMobile ? '3.25rem' : '4.5rem'
  const length = children.length
  return (
    <StyleBox
      position="relative"
      isMobile={isMobile}
      width={width}
      arrowWidth={arrowWidth}
      activeItemIndex={activeItemIndex}
      length={length}
      {...props}
    >
      <Slider
        autoplay={autoplay}
        prevArrow={prevArrow || <Chevron.Left isMobile={isMobile} width={arrowWidth} />}
        nextArrow={nextArrow || <Chevron.Right isMobile={isMobile} width={arrowWidth}  />}
        arrows={arrows}
        dots={false}
        ref={theRef}
        // autoplaySpeed={3500}
        speed={300}
        afterChange={() => {
          if (typeof handleCange === 'function') {
            handleCange(false)
          }
        }}
        beforeChange={(from, to) => {
          setActiveIndex(to)
          if (typeof setType === 'function') {
            setType(to)
          }
          if (typeIndex !== to) {
            handleCange(true)
          }
        }}
        slidesToShow={slidesToShow || 1}
        {...silderProps}
      >
        {children}
      </Slider>
    </StyleBox>
  );
})

const Carousel = forwardRef((props, ref) => {
  return (
    <>
      <Media lessThan="tablet"><MySlider ref={ref} {...props} isMobile /></Media>
      <Media greaterThanOrEqual="tablet"><MySlider ref={ref} {...props} /></Media>
    </>
  )
})

Carousel.defaultProps = {
  arrows: true,
  silderProps: {
    infinite: false,
  }
}

export default Carousel;
