import React, { useEffect, useState } from 'react'
import { useWindowSize } from 'react-use'

import Box from './Box'
import LazyLoad from './LazyLoad'
import Text from './Text'
import { responsive } from './ThemeProvider/theme'

const UpCircle = ({ img, show, title, text, ratio }) => {
  const { width } = useWindowSize()
  const [firstTime, setBrowse] = useState(true)
  useEffect(() => {
    if(firstTime && show) {
      setBrowse(false)
    }
  }, [show, firstTime])
  // console.log(width)
  return (
    <Box
      width="100%"
      position="relative"
      transform={responsive(!firstTime ? 'translateY(0%)' : 'translateY(-100%)', null, !firstTime ? 'translateX(-5%)' : 'translateX(-100%)')}
      transition="all 0.5s"
    >
      <LazyLoad ratio={ratio}>
        {img}
      </LazyLoad>
      <Box.AbsCenter
        textAlign="center"
        color="white"
        left={responsive('50%', null, '12.5%')}
        transform={responsive('translate(-50%, -75%)', null, 'translate(0%, -52.5%)')}
      >
        <Text
          fontWeight="500"
          fontSize={responsive(width && `${width / 18.75}px`, '1.75rem', '2.125em')}
          whiteSpace="pre-wrap"
        >{title || '關鍵投資焦點'}</Text>
        <Box.Inline
          fontSize={responsive(width && `${width / 20.83}px`, '1.5rem', '2.125em')}
          fontWeight="500"
          pt="0.375rem"
          mt="0.375rem"
          borderTop="1px solid white"
        >{text}</Box.Inline>
      </Box.AbsCenter>
    </Box>
  )
}

export default UpCircle
