import React, { useEffect, useState } from 'react'
import { StaticImage } from 'gatsby-plugin-image'
import { useWindowSize } from 'react-use'

import Box from './Box'
import Flex from './Flex'
// import Text from './Text'
import Board from './Board'
import Source from './Source'
import Button from './Button'
import Carousel from './Carousel'
import UpCircle from './UpCircle'
import { responsive, Media } from './ThemeProvider/theme'
import { trackClick } from '../utils/trackEvent'
import LazyLoad from './LazyLoad'

// const BglightOrange = <Box.FullAbs><StaticImage src="../containers/HomePage/light_orange.png" alt="bgblock" /></Box.FullAbs>

const ChartPageModule = ({
    charts,
    show,
    background,
    mobileCircle,
    deskCircle,
    title,
    text,
    nextArrow,
    activeBg,
    sourceBorderColor,
  }) => {
  const [typeIndex, setType] = useState(0)
  const [changing, handleCange] = useState()
  const { width } = useWindowSize()
  const [windowWidth, setWindowSize] = useState()
  useEffect(() => {
    setWindowSize(width)
  }, [width])
  // console.log(width)
  return (
    <Flex
      flexDirection={responsive('column', null, 'row')}
      alignItems="center"
      background={background}
      overflow="hidden"
      pt={responsive(0, null, '2em')}
    >
      <Box width={responsive('100%', '50%', '26%')}>
        <Media lessThan="laptop">
          <UpCircle
            img={mobileCircle}
            title={title}
            text={text}
            show={show}
            ratio={1092 / 451}
          />
        </Media>
        <Media greaterThanOrEqual="laptop">
          <UpCircle
            img={deskCircle}
            title={title}
            text={text}
            show={show}
            ratio={516 / 997}
          />
        </Media>
      </Box>
      <Box px={responsive('1em', '5em')} width={responsive('100%', null, '74%')}>
        <Flex justifyContent={responsive('center', null, 'flex-start')} pb="1em">
          {charts.map(({ buttonText }, k) => (
            <Box position="relative" transform="translate3d(0, 0, 0)" borderRadius="0.5em" overflow="hidden" mx={responsive('0.25em', '0.625em')} key={k}>
              <LazyLoad ratio={361 / 132} opacity={+(k === typeIndex)}>
                {activeBg}
              </LazyLoad>
              <Box position="relative">
                <Button.Transparent
                  fontSize={responsive(`${windowWidth / 20.83}px`, '1.5em')}
                  bg={k !== typeIndex  && 'custom.lightSkin'}
                  _hover={{
                    bg: k !== typeIndex  && 'custom.lightSkin'
                  }}
                  _focus={{
                    bg: k !== typeIndex  && 'custom.lightSkin'
                  }}
                  _active={{
                    bg: k !== typeIndex  && 'custom.lightSkin'
                  }}
                  width="100%"
                  py="0.375rem"
                  disabled={changing}
                  color="white"
                  onClick={() => {
                    setType(k)
                    trackClick(buttonText)
                    if (k !== typeIndex) {
                      handleCange(true)
                      setTimeout(() => handleCange(false), 400)
                    }
                  }}
                >{buttonText}</Button.Transparent>
              </Box>
            </Box>
          ))}
        </Flex>
        <Board>
          <Carousel
            typeIndex={typeIndex}
            handleCange={handleCange}
            setType={setType}
            prevArrow={<StaticImage layout="fullWidth" src="arrow_orange.png" alt="看上一張圖按鈕" />}
            nextArrow={nextArrow}
            mx={responsive('0.5em', 0)}
          >
            {charts.map(({ image_molibe, image_desk, title, source }, i) => (
              <Box key={i}>
                 <Box
                  mx={responsive('1em', '3em')}
                  pt={responsive('3em', '1.5em')}
                  pb={responsive('0.25em', '0.5em')}
                  borderBottom="1px solid"
                  borderColor={sourceBorderColor}
                  textAlign="left"
                  fontSize={responsive(`${windowWidth / 23.4375}px`, '1.25em')}
                  fontWeight="bold"
                  mb="0.5em"
                  color="custom.darkGray"
                >
                  {title}
                </Box>
                <Box px={responsive(0, '5%')}>
                  <Media lessThan="tablet">
                    <LazyLoad ratio={1201 / 1001}>
                      {image_molibe}
                    </LazyLoad>
                  </Media>
                  <Media greaterThanOrEqual="tablet">
                    <LazyLoad ratio={1300 / 551}>
                      {image_desk}
                    </LazyLoad>
                  </Media>
                </Box>
                <Source borderColor={sourceBorderColor}>
                  {source}
                </Source>
              </Box>
            ))}
          </Carousel>
        </Board>
      </Box>
    </Flex>
  )
}

export default ChartPageModule
