const pages = [
  // 'section-focus',
  'ESG-green',
  'new-market',
  'target',
  'section-subject',
  'section-fund',
  'section-buy'
]

const titles = [
  // '投資焦點',
  'ESG綠色永續',
  '新興市場',
  '瞄準新興市場',
  '凱基新興市場ESG永續主題債券基金',
  '基金資料',
  '申購請洽',
].reduce((obj, n, i) => {
  obj[pages[i]] = n
  return obj
}, {})

const comps = [
  // KeyPoint,
  FocusOne,
  FocusTwo,
  NewMarket,
  Esg,
  Fund,
  Buy,
]

