const anime = keyframes`
  0%,,
  100% {
    transform: translateY(15%);
  }
  50% {
    transform: translateY(-15%);
  }
`

const DownAnimation = styled(Box)`
  animation: ${anime} 1s infinite;
`

const AllPages = ({ page }) => {
  return comps.map((p, i) => {
    const show = page === pages[i]
    return (
      <Element name={pages[i]} key={i}>
        {createElement(p, { show })}
      </Element>
    )
  })
}

const IndexPage = () => {
  const { site: { siteMetadata } } = useStaticQuery(graphql`
    query HomeQuery {
      site {
        siteMetadata {
          title
          description
          url
        }
      }
    }
  `)
  const [ref, { height }] = useMeasure();
  const [page, setPage] = useState()
  const [pin, setPin] = useState(true)
  const footerRef = useRef()
  const intersection = useIntersection(footerRef, {
    root: null,
    rootMargin: '0px',
    threshold: 0.1,
  });
  // const [bannerLoaded, setBannerLoaded] = useState()
  // const [inverse, setInverse] = useState()
  const { isOpen, onOpen, onClose } = useDisclosure()
  const { y } = useWindowScroll();
  useEffect(() => {
    window.history.replaceState(null, null, withPrefix(`/index.html${window.location.search}`))
  }, [])

  useEffect(() => {
    if (page) {
      trackView(page, `${titles[page]} | ${siteMetadata.title}`)
    }
  }, [page, siteMetadata])

  return (
    <Layout onOpen={onOpen} siteMetadata={siteMetadata}>
      <Box.Absolute top="-9999px" left="-9999px" fontSize="0" zIndex={-999} opacity="0" visibility="none">
        {/* <Link to="intro" spy onSetActive={(to) => setPage(to)}/> */}
        {pages.map((name) => (
          <Link
            spy
            offset={-360}
            to={name}
            onSetActive={(to) => setPage(to)}
            key={name}
            hashSpy
          />
        ))}
      </Box.Absolute>
      <Box
        as={Element}
        name="intro"
        height={responsive(`calc(100% - ${height}px)`, '100%', 'auto')}
        maxHeight="100vh"
      >
        <Banner />
      </Box>
      <AllPages page={page} />
      <Container ref={footerRef} textAlign="justify" fontWeight="bold" py="3em" fontSize="0.875em">
        凱基投信獨立經營管理 |本公司經理之系列基金經金管會申請核准或申報生效，惟不表示絕無風險。基金經理公司以往之經理績效不保證基金之最低投資收益；基金經理公司除盡善良管理人之注意義務外，不負責基金之盈虧，亦不保證最低之收益，投資人申購前應詳閱基金公開說明書。有關基金應負擔之費用(含分銷費用)已揭露於基金公開說明書，本公司及銷售機構均備有基金公開說明書，投資人亦可至公開資訊觀測站查詢。
        <Text.Inline color="custom.orange">基金投資無受存款保險、保險安定基金或其他相關保證機制之保障，投資人須自負盈虧。基金投資可能產生的最大損失為全部本金。</Text.Inline>
        本資料係整理分析各方面資訊之結果，純屬參考性質，本公司不作任何保證或承諾，請勿將本內容視為對個別投資人做基金買賣或其他任何投資之建議或要約。本公司已力求其中資訊之正確與完整，惟不保證本報告絕對正確無誤。未經授權不得複製、修改或散發引用。
        <Text.Inline color="custom.orange">由於高收益債券之信用評等未達投資等級或未經信用評等，且對利率變動的敏感度甚高，違約造成無法還本的損失以及必須承擔屆時賣方無法履約的風險，敬請故以投資高收益債券為訴求之基金可能會因利率上升、市場流動性下降，或債券發行機構違約不支付本金、利息或破產而蒙受虧損。以投資高收益債券為訴求之基金不適合無法承擔相關風險之投資人，投資人投資以高收益債券為訴求之基金不宜占其投資組合過高之比重。本基金所投資之標的可能投資美國Rule 144A債券，該等債券屬私募性質，較可能發生流動性不足，財務訊息揭露不完整或因價格不透明導致波動性較大之風險，投資人須留意相關風險。本基金承作衍生自信用相關金融商品(即信用違約交換CDS及CDX index與iTraxx Index等經金管會核准之相關商品)僅得為受信用保護的買方，固然可利用信用違約商品來避險，但無法完全規避投資人留意。</Text.Inline>基金涉及投資新興市場部份，因其波動性與風險程度可能較高，且其政治與經濟情勢穩定度可能低於已開發國家，也可能使資產價值受不同程度之影響。本基金得投資轉換公司債，由於該債同時兼具債券與股票之特性，因此除面臨債券之利率風險、流動性風險與信用風險外，還可能因標的股票價格波動，而造成該轉換公司債之價格波動，其利率風險、外匯波動風險或債券發行違約風險都高於一般債券。基金配息率不代表基金報酬率，且過去配息率不代表未來配息率；基金淨值可能因市場因素而上下波動。基金的配息可能由基金的收益或本金中支付。任何涉及由本金支出的部份，可能導致原始投資金額減損。基金由本金支付配息之相關資料，投資人可至本公司網站查詢。基金配息前未先扣除應負擔之相關費用。
        <Text.Inline color="custom.orange">投資遞延手續費N類型者，其手續費之收取將於買回時支付，且該費用將依持有期間而有所不同，其餘費用之計收與前收手續費類型完全相同，亦不加計分銷費用，請參閱基金公開說明書受益人應負擔費用之項目及其計算、給付方式之說明。</Text.Inline>
      </Container>
      <BottomNav setPin={setPin} onOpen={onOpen} ref={ref} />
      <VideoModal onClose={onClose} isOpen={isOpen} />
      {!intersection?.isIntersecting && createElement(y ? HideDuring : Box, { inverse: true }, (
        <Box
          position="fixed"
          left="50%"
          transform="translateX(-50%)"
          bottom={responsive(`calc(${`${pin ? height : 0}px`} + 1em)`, '3em')}
          color={y ? 'custom.textSource' : "white"}
        >
          <DownAnimation>
            <Icon width={responsive('2em', '2.5em')} height={responsive('2em', '2.5em')} as={HiOutlineChevronDoubleDown} />
          </DownAnimation>
        </Box>
      ))}
      {y && (
        <Box
          zIndex={99}
          position="fixed"
          cursor="pointer"
          color="white"
          right="1.25em"
          bottom={responsive('1em', '0.5em')}
          transform={responsive(`translateY(${pin ? -height : 0}px)`, 'none')}
          transition="transform 200ms ease-in-out"
        >
          <IconButton
            as={Link}
            onClick={() => trackClick('BackToTop')}
            offset={-78}
            smooth
            size="sm"
            bg="custom.blue"
            to="intro"
            _hover={{ bg: 'custom.blue' }}
            icon={<IoIosArrowUp />}
          />
        </Box>
      )}
    </Layout>
  )
}

export default IndexPage
