import React, { forwardRef, useMemo } from 'react'
import { AspectRatio } from '@chakra-ui/react';

import Box from '../components/Box'
import Flex from '../components/Flex'
import Button from '../components/Button'
import useCsvButton from './useCsvButton'
import { Media, responsive } from '../components/ThemeProvider/theme'

import icons, { size } from './icons'
import { trackClick } from '../utils/trackEvent';

const TheButton = (props) => {
  const btn = <Button {...props} />
  return (
    <>
      <Media lessThan="tablet">{btn}</Media>
      <Media greaterThanOrEqual="tablet">
        <AspectRatio width="5em" ratio={1}>{btn}</AspectRatio>
      </Media>
    </>
  )
}

const BottomButton = forwardRef(({ onOpen, ...props}, ref) => {
  const buttons = useCsvButton()
  const sideButtons = useMemo(() => buttons.data?.filter(({ id }) => id?.startsWith('side')), [buttons])
  return (
    <Flex
      position={responsive('relative', 'fixed')}
      flexDirection={responsive('row', 'column')}
      bottom={responsive('0', '3rem')}
      w={responsive('100%', 'auto')}
      right={responsive('0', '1.25rem')}
      zIndex={1}
      ref={ref}
      {...props}
    >
      {sideButtons ? sideButtons.map(({ name, link, icon }, i) => (
        <Box width={responsive('50%', 'auto')} mt={i && responsive(0, '0.875rem')} key={i}>
          <TheButton
            width="100%"
            px="0.25rem"
            href={link}
            borderLeft={i && responsive('1px solid white', 'none')}
            fontSize="0.75em"
            borderRadius={responsive('0', '0.5rem')}
            onClick={() => {
              trackClick(name)
              if (icon === 'youtube') {
                onOpen()
              }
            }}
          >
            <Flex
              flexDirection={responsive('row', 'column')}
              alignItems="center"
            >
              {icons[icon] && (
                <Box mb={responsive(0, '0.5em')} mr={responsive('0.5em', 0)}>
                  {icons[icon]}
                </Box>
              )}
              {name}
            </Flex>
          </TheButton>
        </Box>
      )) : <Box flex="1" bg="blue.700" py="0.5rem"><Box height={size} /></Box>}
    </Flex>
  )
})

export default BottomButton
